	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.961500493583415%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"bound lines\"\n        title=\"\"\n        src=\"/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-ad853.png\"\n        srcset=\"/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-1fef5.png 300w,\n/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-cc110.png 600w,\n/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-ad853.png 1200w,\n/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-de57a.png 1800w,\n/static/bound-lines-43ea5a6d74467f72953b4bc5d7007686-a16d0.png 2026w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<h1>Brief</h1>\n<p><em>Bound Lines</em> is a digital print on metal that visualizes 18th century ship logs and reveals the “footprints” of sailors as they explored the world.</p>\n<h1>Antillean Vision</h1>\n<p><em>Bounds Lines</em> is currently in the juried show “Antillean Vision: Maps and the Making of the Caribbean” at the Lowe Art Museum in Florida.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.31021897810219%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"gallery view\"\n        title=\"\"\n        src=\"/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-ad853.png\"\n        srcset=\"/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-1fef5.png 300w,\n/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-cc110.png 600w,\n/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-ad853.png 1200w,\n/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-de57a.png 1800w,\n/static/gallery-view-16cd8401c7167a5246e7a772fc38ea5d-ba77c.png 2192w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 71.5162138475022%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"wall text\"\n        title=\"\"\n        src=\"/static/wall-text-26611484778d1b88ce2fd2a7668f252c-ad853.png\"\n        srcset=\"/static/wall-text-26611484778d1b88ce2fd2a7668f252c-1fef5.png 300w,\n/static/wall-text-26611484778d1b88ce2fd2a7668f252c-cc110.png 600w,\n/static/wall-text-26611484778d1b88ce2fd2a7668f252c-ad853.png 1200w,\n/static/wall-text-26611484778d1b88ce2fd2a7668f252c-de57a.png 1800w,\n/static/wall-text-26611484778d1b88ce2fd2a7668f252c-4d5c8.png 2282w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<h1>Motivation</h1>\n<p>We began with the question of whether the data from historic ships’ logs could be used as a drawing tool. Much like the exercise of drafting negative space rather than the object itself, we have decided to create images of the world by following the travels of early sailors as they traced their way around the globe - gradually feeling out the forms of our continents. Each individual thread represents the travels of an individual European ship from the 18th or 19th century. The visuals are generated computational from the <a href=\"http://pendientedemigracion.ucm.es/info/cliwoc/\">CLIWOC</a> dataset.</p>\n<h1>Sources</h1>\n<ul>\n<li><a href=\"http://pendientedemigracion.ucm.es/info/cliwoc/\">Climatological Database for the World’s Oceans 1750-1850</a></li>\n<li><a href=\"https://github.com/mikewesthad/ship-climate-data\">Code repository</a></li>\n</ul>","frontmatter":{"date":"March 11, 2018","title":"Bound Lines","subtitle":"Digital print on metal, 30\" x 16\", 2018"}}},"pathContext":{}}

